import React from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { useSelector, useDispatch } from 'react-redux';
import { selectIsAuthenticated, selectCurrentUser, logout } from '@store/slices/authSlice';
import LanguageSwitcher from '@components/LanguageSwitcher';
import './Header.scss';

const Header: React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const isAuthenticated = useSelector(selectIsAuthenticated);
  const currentUser = useSelector(selectCurrentUser);

  const handleLogout = () => {
    dispatch(logout());
    navigate('/');
  };

  return (
    <header className="header">
      <div className="container">
        <div className="header__content">
          <div className="header__logo">
            <Link to="/">
              <img src="/logo.svg" alt="Mart.az" />
            </Link>
          </div>

          <nav className="header__nav">
            <ul className="header__menu">
              <li>
                <Link to="/">{t('home')}</Link>
              </li>
              <li>
                <Link to="/listings">{t('listings')}</Link>
              </li>
              <li>
                <Link to="/categories">{t('categories')}</Link>
              </li>
            </ul>
          </nav>

          <div className="header__actions">
            <LanguageSwitcher />
            
            {isAuthenticated ? (
              <div className="header__user-menu">
                <span className="header__username">
                  {currentUser?.firstName} {currentUser?.lastName}
                </span>
                <div className="header__dropdown">
                  <Link to="/profile" className="header__dropdown-item">
                    {t('profile')}
                  </Link>
                  <Link to="/my-listings" className="header__dropdown-item">
                    {t('myListings')}
                  </Link>
                  <button 
                    className="header__dropdown-item header__logout" 
                    onClick={handleLogout}
                  >
                    {t('logout')}
                  </button>
                </div>
              </div>
            ) : (
              <div className="header__auth">
                <Link to="/login" className="btn btn--secondary">
                  {t('login')}
                </Link>
                <Link to="/register" className="btn btn--primary">
                  {t('register')}
                </Link>
              </div>
            )}
            
            <Link to="/create-listing" className="btn btn--accent">
              {t('postAd')}
            </Link>
          </div>
        </div>
      </div>
    </header>
  );
};

export default Header; 